}

html:not(.touchevents) a:focus {
  text-decoration: underline;
  outline: none;
}

a.underline {
  text-decoration: underline;
}

a.no-underline {
  text-decoration: none !important;
}

a.white {
  color: #ffffff;
}

a.gray {
  color: #667284;
}

a.active {
  pointer-events: none;
}

a.m-muted {
  text-decoration: underline;
  color: #001432;
}

html:not(.touchevents) a.m-muted:hover {
  color: #0064fe;
}

a.profile {
