/**
 * General style definitions for menubar navigation list.
 *
 * @section Navigation
 *
 * @page List elements
 *
 * @example
 * <nav class="main-navigation" role="navigation">
 * <ul role="menubar">
 * <li>
 * <a aria-selected="false" href="register_company.html">Register Company</a>
 * </li>
 * <li>
 * <a aria-selected="false" href="profile_data.html">Profile Data</a>
 * </li>
 * <li>
 * <a aria-selected="false" href="business_profile.html">Business Profile</a>
 * </li>
 * <li>
 * <a aria-selected="false" href="help.html">Help</a>
 * </li>
 * </ul>
 * </nav>
 */
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

[role="menubar"] {
  margin: 0;
  padding: 0;
  list-style: none;
}

.sub-navigation {
  line-height: 200%;
}

@media (min-width: 321px) {
  .sub-navigation {
    display: none;
    visibility: hidden;
  }
}

.list-composite {
  list-style: none;
  margin: 0;
  padding: 0;
}

.list-composite__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem;
}

.mobile-navigation {
  z-index: 20;
  position: absolute;
  display: block;
  left: 0;
  top: 9rem;
  width: 100%;
  height: 100%;
}

.content-list-item {
  max-width: 34rem;
  height: 6rem;
  border: 1px solid #ccd0d6;
  border-top: none;
}

.content-list-item p {
  display: flex;
  flex-direction: column;
}

.content-list-item:first-child {
  border-top: 1px solid #ccd0d6;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
}

.content-list-item:nth-last-child(-n + 1) {
  border-bottom: 1px solid #ccd0d6;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}

@media (min-width: 321px) {
  .content-list-item {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
    border-radius: 0;
    border-right: none;
    border-left: none;
  }
  .content-list-item:first-child {
