.m-page {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.m-page--grey {
  background-color: #f2f3f5;
}

.m-page--lightBlue {
  background-color: #f2f7ff;
}

.m-page-container {
  position: relative;
  flex: 1 1 auto;
  margin-top: 2rem;
  padding: 0 1rem 1rem;
}

.m-page-container-isCentered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

/* Todo: these are app specific styles and should not be part of the library */
.authentication-container {
  display: flex;
  align-items: stretch;
  align-content: space-between;
  margin: 0 1rem 1rem 1rem;
}

.authentication-container :first-child {
  margin-right: 0.5rem;
}

.authentication-container :last-child {
  margin-left: 0.5rem;
}

@media (max-width: 321px) {
  .authentication-container {
    margin: 1rem;
  }
  .authentication-container button {
    font-size: 90%;
  }
}

/* TODO: Remove intro-header, intro-title, info-icon after extra/DCx components are used within digital card app */
.intro-header {
  margin-top: 3.75rem;
}

.intro-title {
  font-size: 1.625rem;
  font-weight: 600;
  color: #ffffff;
  margin-top: 1rem;
}

.info-icon {
  position: absolute;
  top: 3rem;
  right: 2rem;
}

/* Todo: refactor css classnames for MCSS style */
.m-panel {
  display: block;
  /* Only for IE11, which actually supports display: flex, but does not support @supports .. */
  background-color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(128, 128, 128, 0.2);
  border-radius: 3px;
}

@supports (display: flex) {
  .m-panel {
    display: flex;
    flex: 1;
    flex-direction: column;
  }
}

.m-panel + .m-panel {
  margin-top: 3rem;
}

.m-panel-header {
  display: flex;
  align-items: center;
  color: #002d72;
  background-color: #ffffff;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  border-bottom: 1px solid #e6e8eb;
  height: 4rem;
  padding: 0 0.75rem 0 1.5rem;
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 1.625rem;
}

@media (min-width: 768px) {
  .m-panel-header {
    font-weight: bold;
    font-size: 1.125rem;
    line-height: 1.5rem;
  }
}

.m-panel-content {
  font-size: 1rem;
  flex-grow: 1;
}

.m-panel-content:after {
  content: ".";
  clear: both;
  display: block;
  visibility: hidden;
  height: 0;
  overflow: hidden;
}

.m-panel-contentPadding {
  margin: 1.5rem;
}

@media (min-width: 1024px) {
  .m-panel-contentPadding {
    margin: 3rem 6rem;
  }
}

.m-panel-footer {
  height: 64px;
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #e6e8eb;
}

.m-panel-footer-isStack {
  height: auto;
  flex-direction: column;
}

.m-panel-footer-isStack .m-button-block:not(:last-child) {
  margin-bottom: 0;
}

@media screen and (max-width: 768px) {
  .m-panel .m-panel {
    margin-bottom: 0;
    border-radius: 0;
    box-shadow: none;
  }
  .m-panel .m-panel-content {
    padding: 0;
  }
  .m-panel .m-panel-contentPadding {
    margin: 1rem 1.5rem;
  }
  .m-panel-footer {
    padding: 0 1rem;
    justify-content: space-around;
  }
}

/*.panel-header *:last-child {
    margin-left: auto;
    margin-right: 0.5rem;
  }*/
.m-icon {
  width: 1.5em;
  height: 1.5em;
}

.m-icon-isRotate {
  animation: 1s icon-rotate ease-in-out infinite;
}

@keyframes icon-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

.m-icon * {
  fill: #667284;
  fill: currentColor !important;
  stroke-width: 1.5px !important;
}

.m-input {
  margin-bottom: 1.5rem;
  /* Hide clear and reveal icons in internet explorer */
}

.m-input-isStandalone {
  margin-bottom: 0;
}

.m-input ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #667284;
}

.m-input ::-moz-placeholder {
  /* Firefox 19+ */
  color: #667284;
}

.m-input :-ms-input-placeholder {
  /* IE 10+ */
  color: #667284;
}

.m-input :-moz-placeholder {
  /* Firefox 18- */
  color: #667284;
}

.m-input-elementWrapper {
  position: relative;
}

.m-input-element {
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 100%;
  border: 1px solid #ffffff;
  box-shadow: 0 0 0 1px #ccd0d6;
  font-size: 1rem;
  font-family: inherit;
  padding: 0 0.75rem;
  height: 2.5rem;
  color: #001432;
  border-radius: 2px;
  transition: 0.125s ease-in-out 0s;
  transition-property: border-color;
}

.m-input-element:focus {
  color: #33435b;
  outline: none;
  border: 1px solid #66a2fe;
  box-shadow: 0 0 0 1px #66a2fe;
}

.m-input-element:disabled {
  pointer-events: none;
  color: #99a1ad;
  background-color: #f2f3f5;
  border: 1px solid transparent;
  box-shadow: 0 0 0 1px #ccd0d6 !important;
}

.m-input-element:before {
  content: "";
}

.m-input-element[aria-readonly="true"] {
  padding-left: 2rem;
  background-color: #f2f3f5;
  border: 1px solid transparent;
  box-shadow: 0 0 0 1px #ccd0d6;
  pointer-events: none;
}

.m-input-element[aria-readonly="true"]:focus {
  outline: 0 none;
  box-shadow: 0 0 0 1px #ccd0d6;
}

.m-input-element.m-input-isValid {
  border: 1px solid #85d9b4;
  box-shadow: 0 0 0 1px #85d9b4;
}

.m-input-element.m-input-hasInfo {
  border: 1px solid #66a2fe;
  box-shadow: none;
}

.m-input-element.m-input-hasWarning {
  border: 1px solid #eb8676;
  box-shadow: 0 0 0 1px #eb8676;
}

.m-input-element.m-input-hasError {
  border: 1px solid #eb8676;
  box-shadow: 0 0 0 1px #eb8676;
}

.m-input-element.m-input-hasIcon {
  padding-right: 2.75rem;
}

.m-input input[type="search"] {
  -webkit-appearance: textfield;
}

