  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  margin: auto;
  animation: spinner-rotate 2s linear infinite;
  transform-origin: center center;
}

.m-spinner-path {
  stroke-dasharray: 89, 200;
  stroke-dashoffset: 0;
  color: inherit;
  stroke: currentColor;
  stroke-width: inherit;
  animation: spinner-rotate-dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
}

