.m-fullscreenDropzone {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(52, 192, 130, 0.8);
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  transition: z-index, visibility, opacity;
  transition-delay: 0.15s, 0.15s, 0s;
  transition-duration: 0s, 0s, 0.15s;
}

.m-fullscreenDropzone-isVisible {
  transition-delay: 0s, 0s, 0s;
  z-index: 2020;
  visibility: visible;
  opacity: 1;
}

.m-fullscreenDropzone-content {
  position: absolute;
  top: 0.9375rem;
  left: 0.9375rem;
  right: 0.9375rem;
  bottom: 0.9375rem;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px dashed white;
}

.m-fullscreenDropzone-label {
  margin: 1rem 0;
  color: #ffffff;
  font-size: 1.25rem;
  font-weight: bold;
}

.m-fullscreenDropzone input[type="file"] {
