}

.m-colorSelect {
  margin: 1.5rem 0;
}

.m-colorSelect-label {
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.5rem;
  display: block;
  margin: 1.5rem 0;
  color: #000000;
}

.m-colorSelect-option {
  display: inline-block;
  padding: 0;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: 0 none;
  background: transparent;
  appearance: none;
  cursor: pointer;
  position: relative;
  width: 3rem;
  height: 3rem;
  margin: 0 0.75rem 0.75rem 0;
  border-radius: 50%;
  color: #ffffff;
  line-height: 0;
  vertical-align: middle;
  transform: scale(1);
  transition: 250ms transform;
}

.m-colorSelect-option:active {
  transform: scale(1.1);
  transition-duration: 50ms;
}

.m-colorSelect-option:focus {
  outline: none;
}

html:not(.touchevents) .m-colorSelect-option:focus {
  box-shadow: inset 0 0 0 0.25rem rgba(0, 0, 0, 0.15);
}

.m-colorSelect-checkIcon {
  position: absolute;
  top: 0.625rem;
  left: 0.625rem;
  right: 0.625rem;
  bottom: 0.625rem;
  width: 1.75rem;
  height: 1.75rem;
}

.m-colorSelect-checkIcon polyline {
  fill: none;
  stroke: #ffffff;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 1.125rem;
  stroke-dashoffset: 1.125rem;
  will-change: stroke-dashoffset;
  transition: stroke-dashoffset 0.15s cubic-bezier(0.4, 0, 0.2, 1);
}

.m-colorSelect-option-isSelected .m-colorSelect-checkIcon polyline {
  stroke-dashoffset: 0rem;
}

.m-microAnimation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.m-microAnimation-pulse {
  transform: translate(-6.375rem, -6.375rem);
}

.m-microAnimation-pulse span {
  position: absolute;
  top: 0;
  left: 0;
  width: 12.75rem;
  height: 12.75rem;
  border-radius: 50%;
  transform: scale(0);
  background-color: rgba(0, 100, 254, 0.9);
}

.m-microAnimation-pulse-isPlaying span {
  animation: 1500ms m-ma-pulse cubic-bezier(0.12, 0.22, 0.17, 0.98);
}

@keyframes m-ma-pulse {
  0% {
    opacity: 1;
    transform: scale(0.01);
  }
  100% {
    opacity: 0.001;
    transform: scale(1);
  }
}

